import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup1 } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';

const Growth = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [growthData, setData] = useState(null);
  const [resultData, setResultData] = useState(null);
  const [selectedData, setSelectedData] = useState(null);
  const [selectedBubbleData, setSelectedBubbleData] = useState(null);
  const [idState, setReferenceID] = useState(null);
  const [bubbleName, setBubbleName] = useState(null);
  const [bubbleSelection, setBubbleSelection] = useState(null);

  // Axios call
  const fetchData = async () => {
    setIsLoading(true);

    await Promise.all([
      axios(`${API_BASE_URL}/total-revenue/growth.json?print=pretty`),
      axios(`${API_BASE_URL}/total-revenue/growth/segment_ranking.json?print=pretty`)
    ]).then(response => {
      const result = response[0].data;
      const resultSelected = response[1];
      setData(result);
      setResultData(resultSelected);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setIsLoading(false);
    });;

  };

  useEffect(() => {
    let isMounted = true;

    if (isMounted) fetchData();

    return () => { isMounted = false };
  }, []);

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      }
    })
  }

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onCloseFilter = () => {
    setShowFilter(false);
  }
  const onShowFilter = () => {
    setShowFilter(true)
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
      <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <RadioDD
              title="Measure"
              id="measure"
              options={optionMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              id="range"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Show"
              id="show_by"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionGroup1}
            />
          </div>

          <FilterModal
            title="Topline"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
          >
            <RadioDD
              title="Measure"
              id="measure2"
              options={optionMeasure}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Show by"
              id="show_by2"
              options={optionGroup1}
              visibleInModal
            />
          </FilterModal>
        </FilterContainer>
        <GrowthContent
          bubbleName={bubbleName}
          selectedData={selectedData}
          bubbleSelection={bubbleSelection}
          idState={idState}
          width={width}
          resultData={resultData}
          selectedBubbleData={selectedBubbleData}
          setSelectedData={setSelectedData}
          setSelectedBubbleData={setSelectedBubbleData}
          setBubbleName={setBubbleName}
          setReferenceID={setReferenceID}
          setBubbleSelection={setBubbleSelection}
          growthData={growthData}
        />
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Growth;