import React from 'react';
import { useHistory, Link } from 'react-router-dom';
import { Form, Button, Alert, Row, Col } from 'react-bootstrap';
import { ReactComponent as Logo } from '../../assets/images/orbis-logo-dark.svg';
import { ReactComponent as BoyOrange } from '../../assets/images/boy-orange.svg';
import { ReactComponent as GirlDarkBlue } from '../../assets/images/girl-dark-blue.svg';
import { ReactComponent as BoyTeacher } from '../../assets/images/boy-teacher.svg';
import { ReactComponent as BoyOrangeBack} from '../../assets/images/boy-orange-back.svg';
import { ReactComponent as GirlTurquoise} from '../../assets/images/girl-turquoise.svg';
import { ReactComponent as BoyViolet} from '../../assets/images/boy-violet.svg';
import { ReactComponent as Plane } from '../../assets/icons/plane.svg';
import { ReactComponent as Shutter } from '../../assets/icons/shutter.svg';
import { ReactComponent as Flare } from '../../assets/icons/flare.svg';
import { ReactComponent as Add } from '../../assets/icons/add-big.svg';
import { BiChevronDown } from 'react-icons/bi';
import { VscLoading } from "react-icons/vsc";
import MonitorSection1 from '../../assets/images/monitor-total-revenue-deep-dive.png';
import LaptopSection3 from '../../assets/images/laptop-total-revenue-deep-dive.png';
import LaptopSection4 from '../../assets/images/laptop-snapshot-builder.png';
import LaptopSection5 from '../../assets/images/laptop-account-navigator.png';


import './login.scss';
import { useState } from 'react';

const Login = () => { 
  const [username, setUsername] = useState('');
  const [isError, setError] = useState(false);
  const [isLoading, setLoading] = useState(false);
  
  const onChange = (e) => {
    setUsername(e.target.value)
  }

  let history = useHistory();

  const onKeyUp = () => {
    setError(false)
  }

  const onSubmit = (e) => {
    e.preventDefault();
    setLoading(true)
    setTimeout(() => {
      if( username === 'gleng@equinix.com'){
        localStorage.setItem("username", username);
        history.push("/sso-equinix-com");
        setLoading(false)
      } else {
        setError(true)
        setLoading(false)
      }
    }, 2000)
  }

  return (
    <div className="login-page">
      <Row className="section-1">
        <Col xl={6} lg={12} className="text-center">
          <div className="login-block">
            <div className="logo">
              <Logo />
            </div>
            <p>Welcome back! Please login to your account.</p>
            <Form onSubmit={onSubmit}>
              <Form.Group controlId="username">
                {isError && <Alert variant="danger">We cant find your account or your Username is incorrect.</Alert>}
                <Form.Control 
                  type="text" 
                  placeholder="Username" 
                  name="username" 
                  onKeyUp={onKeyUp}
                  value={username} 
                  required 
                  onChange={onChange} 
                />
              </Form.Group>
              <div className="btn-block">
                <Button 
                  type="submit" 
                  className="btn-login"
                  disabled={isLoading ? true : false}
                >
                  Login 
                  {isLoading && <VscLoading className="is-loading"/>}
                </Button>
                <Button variant="light" className="btn-request">Request Access</Button>
              </div>
              <Link to="/login" className="terms">Term of use. Privacy policy</Link>
            </Form>
          </div>
          <div>
            <a className="btn btn-link btn-scroll" href="#section2"><BiChevronDown viewBox="7 8 10 10" className="icon" /> Scroll Down to Learn More</a>
          </div>
        </Col>
        <Col xl={6} lg={12}>
          <div className="image-block">
            <img src={MonitorSection1} alt="total-revenue-deep-dive" className="img-fluid"/>
            <div  className="person person-1"><BoyOrange/></div>
            <div className="person person-2"><GirlDarkBlue/></div>
          </div>
        </Col>
      </Row>

      <div className="wrapper">
        <Row className="section-2" id="section2">
        <Col>
          <h2 className="title">Featured Dashboards that We Provide</h2>
          <ul className="featured-list">
            <li><a href="/"><Plane className="icon"/>EXECUTIVE COCKPIT</a></li>
            <li><a href="/"><Shutter className="icon"/>SNAPSHOT BUILDER</a></li>
            <li><a href="/"><Flare className="icon"/>CUSTOMER NAVIGATOR</a></li>
            <li><a href="/"><Add className="icon"/>AND MORE</a></li>
          </ul>
        </Col>
      </Row>

        <Row className="section-3">
        <Col xl={6}>
          <div className="image-block">
            <img src={LaptopSection3} alt="laptop-total-revenue-deep-dive" className="img-fluid" width="726px"/>
            <div  className="person person-1"><BoyTeacher/></div>
          </div>
        </Col>
        <Col xl={6}>
          <h2 className="title">Essential analytics for agile decision-making</h2>
          <p className="desc">
            Make smarter decisions without drowning in information through our single repository of essential dashboards required to lead your organization. 
          </p>
          <Button className="btn-learn">Learn More</Button>
        </Col>
      </Row>

        <Row className="section-4">
        <Col xl={6}>
          <h2 className="title">Mindfully-designed data visualizations that saves time</h2>
          <p className="desc">
            Effortlessly and instantly create dashboards without compromising the quality and integrity of your analytics and visuals.
          </p>
          <Button className="btn-learn">Learn More</Button>
        </Col>
        <Col xl={6}>
          <div className="image-block">
            <img src={LaptopSection4} alt="laptop-snapshot-builder" className="img-fluid" width="726px"/>
            <div  className="person person-1"><BoyOrangeBack/></div>
          </div>
        </Col>
      </Row>

        <Row className="section-5">
        <Col xl={6}>
          <div className="image-block">
            <img src={LaptopSection5} alt="laptop-account-navigator" className="img-fluid" width="726px"/>
            <div  className="person person-1"><GirlTurquoise/></div>
            <div  className="person person-2"><BoyViolet/></div>
          </div>
        </Col>
        <Col xl={6}>
          <h2 className="title">Customer knowledge that produces leverage</h2>
          <p className="desc">
            Gain leverage with our rich customer knowledgebase that enables you to seize opportunities, personalize interactions, and reinforce relationships with your customers.
          </p>
          <Button className="btn-learn">Learn More</Button>
        </Col>
      </Row>
      </div>
    </div>
  );
};

export default Login;
