import React from 'react';
import { optionMetrics } from '../../constants/filterOptions';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';

import ViewReportContent from './ViewReportContent'
import './snapshot.scss';



const ViewReport = () => {
    return (
        <>
            <div className={`page-content view-report-content-page`}>
                <SearchCheckboxDD
                    title="Metric"
                    id="metric"
                    options={optionMetrics}
                />
                <ViewReportContent />
            </div>
        </>
    );
}

export default ViewReport;
