import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapwithPieChart from '../../components/chart/MapwithPieChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';

import CustomTable from '../../components/customTable/CustomTable';


const MatchmakerContent = (props) => {
  const { matchmakerData } = props;

  // Start Table
  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'rank',
      text: 'RANK',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank'
    },
    {
      dataField: 'partner',
      text: 'PARTNER',
      sort: true,
      align: 'left',
      headerAlign: 'left',
      classes: 'partner',
      headerClasses: 'partner'
    },
    {
      dataField: 'vertical',
      text: 'VERTICAL',
      sort: true,
      align: 'left',
      headerAlign: 'left'
    },
    {
      dataField: 'countries',
      text: 'COUNTRIES',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'metros',
      text: 'METROS',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'total_ic_oppty_number',
      text: 'TOTAL IC OPPTY #',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'total_ic_oppty_usd',
      text: 'TOTAL IC OPPTY $',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'percent_capture',
      text: '% CAPTURE',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'est_upside_oppty_number',
      text: 'EST UPSIDE OPPTY #',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'est_upside_oppty_usd',
      text: 'EST UPSIDE OPPTY $',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    }
  ]
  // End Table

  return (
    <Container className="overview" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>total ic opportunity</Card.Title>
              <Kpi data={matchmakerData.overall}
                total={matchmakerData.overall.total}
                withPercent={true} />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card>
            <Card.Body>
              <Card.Title>ecosystem partners mix</Card.Title>
              <Legend legend={matchmakerData.ecosystem_partners_mix.legend} />
              <div style={{ height: '400px' }}>
                <MapwithPieChart id="ecosystem_partners_mix"
                  data={matchmakerData.ecosystem_partners_mix.data}
                  legend={matchmakerData.ecosystem_partners_mix.legend}
                />
              </div>
            </Card.Body>
          </Card>

        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>% capture per vertical</Card.Title>
              <Legend legend={matchmakerData.percent_capture_per_vertical.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="percent_capture_per_vertical_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={matchmakerData.percent_capture_per_vertical.data}
                  legend={matchmakerData.percent_capture_per_vertical.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="PartnerRanking revenue">
            <Card.Body>
              <Card.Title>partner ranking</Card.Title>
              <CustomTable
                tableName='partner-ranking scrollable'
                data={matchmakerData.partner_ranking.by_rank === undefined ? [] : matchmakerData.partner_ranking.by_rank}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default MatchmakerContent;