import React, { useRef } from 'react';
import { useWidowResize } from '../../constants/utilityHooks';
import { Modal, Button } from 'react-bootstrap';
import { motion, AnimatePresence } from 'framer-motion';
import { ReactComponent as Map } from '../../assets/icons/map.svg';
import { ReactComponent as Building } from '../../assets/icons/building.svg';
import { ReactComponent as Flag } from '../../assets/icons/flag.svg';
import { ReactComponent as Globe } from '../../assets/icons/globe.svg';
import { ReactComponent as Profile } from '../../assets/icons/profile.svg';
import { ReactComponent as Team } from '../../assets/icons/team.svg';
import './productModal.scss';

import ResponsiveBasicDD from '../../components/customDropdown/ResponsiveBasicDD';

// loop
const step1List = [
  {
    icon: <Globe className="icon" />,
    key: 'global_report',
    label: 'MACROENVIRONMENT',
  },
  {
    icon: <Map className="icon" />,
    key: 'regional_report',
    label: 'COLO MARKET SIZE AND GROWTH',
  },
  {
    icon: <Flag className="icon" />,
    key: 'country_report',
    label: 'COMPETITOR SCORECARDS',
  },
  {
    icon: <Building className="icon" />,
    key: 'metro_report',
    label: 'PRODUCT ADOPTION',
  },
  {
    icon: <Team className="icon" />,
    key: 'sales_team_report',
    label: 'SALES PERFORMANCE',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'CUSTOMER SATISFACTION',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'ESCALATIONS',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'DIVERSITY, INCLUSION AND BELONGING',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'DATA QUALITY MONITOR',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'TROUBLE TICKET REPORT',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'OPEN ORDER REPORT',
  },
  {
    icon: <Profile className="icon" />,
    key: 'coustomer_report',
    label: 'DATA MART REPORT GENERATOR',
  }
]

const BuilderModal = (props) => {
  const { showBuilderModal, onCancelModal } = props;

  const onCancel = async () => {
    await onCancelModal()
  }

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  return (
    <>
      <Modal
        show={showBuilderModal}
        onHide={onCancel}
        className="product-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">
          <AnimatePresence exitBeforeEnter>
            <ContentContainer
              modalTitle='CHOOSE PRODUCT TO ADD'
              width={width}
            >

              <Modal.Body>
                {step1List && step1List.map((item) => {
                  return (
                    <div className="btn-container" >
                      <Button
                        variant="link"
                        id={item.key}
                      >
                        {item.icon}{item.label}
                      </Button>
                    </div>
                  )
                })}
              </Modal.Body>
              <Modal.Footer>
                <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                <Button variant="primary">Next</Button>
              </Modal.Footer>
            </ContentContainer>
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  modalTitle,
  children, width
}) => {
  return (
    <motion.div className={`content-container`}
      animate='visible'
    >
      {modalTitle &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
          <ResponsiveBasicDD
            className="ml-auto"
            id="groupCategory"
            title="All Categories"
            addTitleOnWide
            wide={width >= 500 ? true : false}
            narrow={width < 500 ? true : false}
            options={[
              {
                key: 'exective_viewpoints',
                text: 'Executive Viewpoints'
              },
              {
                key: 'analytics',
                text: 'Analytics'
              },
              {
                key: 'dashboard',
                text: 'Dashboard'
              },
              {
                key: 'operational_reports',
                text: 'Operational Reports'
              },
              {
                key: 'useful_tools_and_resources',
                text: 'Useful Tools and Resources'
              }
            ]}
          />
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default BuilderModal;