import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup1, optionRegion, optionMetro } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import OverviewContent from './OverviewContent';

const Overview = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [overviewData, setOverviewData] = useState([])

  // Axios call
  const fetchPretty = () => {
    setIsLoading(true)
    axios({
      method: 'get',
      url: `${API_BASE_URL}/total-revenue/overview.json`,
    }).then(result => {
      setOverviewData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchPretty();
  }, []);

  const segment_details = overviewData ? overviewData.segment_details : [];
  const overall = overviewData ? overviewData.overall : [];

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onCloseFilter = () => {
    setShowFilter(false);
  }
  const onShowFilter = () => {
    setShowFilter(true)
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;

  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <CheckboxDD
              title="Region"
              id="measeure"
              options={optionRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
            />
            <RadioDD
              title="Measure"
              id="measeure"
              options={optionMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              addTitleOnWide
              wide={width >= 1516 ? true : false}
              narrow={width < 1516 ? true : false}
              options={optionGroup1}
            />
          </div>

          <FilterModal
            title="Topline"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
          >
            <CheckboxDD
              title="Region"
              id="measeure"
              options={optionRegion}
              visibleInModal
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
              visibleInModal
            />
            <RadioDD
              title="Measure"
              id="measeure"
              options={optionMeasure}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              options={optionRange}
              visibleInModal
            />
            <ResponsiveRadioDD
              title="Group by"
              id="group"
              options={optionGroup1}
              visibleInModal
            />
          </FilterModal>
        </FilterContainer>
        <OverviewContent
          segment_details={segment_details}
          overall={overall}
          overviewData={overviewData}
        />
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Overview;
