import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { ButtonGroup, Button } from 'react-bootstrap';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionAccounts, optionProspect, optionMeasure, optionRange, optionRegion, optionMetro } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import FilterContainer from '../../components/filter/FilterContainer';
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import RadioDD from '../../components/customDropdown/RadioDD'
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';

//pages
import InterconnectionContent from './InterconnectionContent';
import PartnersContent from './PartnersContent';
import MatchmakerContent from './MatchmakerContent';

const Ecosystem = () => {
  const [interconnectionData, setInterconnectionData] = useState([])
  const [partnersData, setPartnersData] = useState([]);
  const [matchmakerData, setMatchmakerData] = useState([]);
  const [isLoading, setIsLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('interconnection');

  // Axios call
  const fetchData = async () => {
    setIsLoading(true)
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/ecosystems/interconnection.json?print=pretty`,
    }).then(result => {
      setInterconnectionData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/ecosystems/partners.json?print=pretty`,
    }).then(result => {
      setPartnersData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/account-navigator/ecosystems/matchmaker.json?print=pretty`,
    }).then(result => {
      setMatchmakerData(result.data);
      setIsLoading(false)
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onCloseFilter = () => {
    setShowFilter(false);
  }
  const onShowFilter = () => {
    setShowFilter(true)
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;
  console.log(matchmakerData);
  return (
    <>
      <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
        <FilterContainer
          className={`${width}`}
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters">
            <ButtonGroup className="sub-tab">
              <Button variant='light' onClick={() => {
                setActiveTab('interconnection')
              }}>
                Interconnection
                            </Button>
              <Button variant='light' onClick={() => {
                setActiveTab('partners')
              }}>
                Partners
                            </Button>
              <Button variant='light' onClick={() => {
                setActiveTab('matchmaker')
              }}>
                Matchmaker
                            </Button>
            </ButtonGroup>
            <Separator height='39px' />
            <CheckboxDD
              title="Region"
              id="measeure"
              options={optionRegion}
            />
            <SearchCheckboxDD
              title="Metro"
              id="metro"
              options={optionMetro}
            />
            <RadioDD
              title="Measure"
              id="measeure"
              options={optionMeasure}
            />
            <Separator height='39px' />
            <ResponsiveRadioDD
              title="Range by"
              id="range2"
              wide={width >= 1394 ? true : false}
              narrow={width < 1394 ? true : false}
              options={optionRange}
            />
            {/* <ResponsiveRadioDD
                            id="accounts"
                            wide={width >= 1394 ? true : false}
                            narrow={width < 1394 ? true : false}
                            options={optionAccounts}
                        />
                        <Separator height='39px' />
                        <ResponsiveRadioDD
                            id="include_only"
                            addTitleOnWide
                            wide={width >= 1516 ? true : false}
                            narrow={width < 1516 ? true : false}
                            options={optionProspect}
                        /> */}
          </div>

          <FilterModal
            title="Navigator"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
          >
            <ResponsiveRadioDD
              id="accounts_modal"
              options={optionAccounts}
              visibleInModal
            />
            <ResponsiveRadioDD
              id="include_only_modal"
              options={optionProspect}
              visibleInModal
            />
          </FilterModal>
        </FilterContainer>

        {/* ============= Your Component here! ============= */}
        
        {activeTab === 'interconnection' && <InterconnectionContent interconnectionData={interconnectionData ? interconnectionData[0] : []} />}
        {activeTab === 'partners' && <PartnersContent partnersData={partnersData ? partnersData[0] : []} />}
        {activeTab === 'matchmaker' && <MatchmakerContent matchmakerData={matchmakerData ? matchmakerData[0]: []} />}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Ecosystem;
